:root {
  --Dark-Grayish: hsl(217, 19%, 35%);
  --Desaturated-Blue: hsl(214, 17%, 51%);
  --Grayish: hsl(212, 23%, 69%);
  --Blue: hsl(210, 46%, 95%);
}

:root {
  --font-inter: font-family: "Outfit", sans-serif;
}

html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: var(--Blue);
}

.article {
  margin-top: 6.25rem;
  justify-content: center;
  align-content: center;
}
@media (min-width: 62.5em) {
  .article {
    justify-content: 0;
    align-content: 0;
  }
}
.article__grid {
  display: grid;
  grid-template-columns: 20.625rem;
}
@media (min-width: 62.5em) {
  .article__grid {
    grid-template-columns: 280px 445px;
  }
}
@media (min-width: 62.5em) {
  .article__image {
    grid-column: 1/2;
  }
}
.article__image img {
  display: block;
  width: 20.5rem;
  height: 12.5rem;
}
@media (min-width: 62.5em) {
  .article__image img {
    width: 17.5rem;
    height: 17.5rem;
  }
}
.article__body {
  background: white;
  padding: 32px 42px 20px 32px;
}
@media (min-width: 62.5em) {
  .article__body {
    grid-column: 2/3;
  }
}
.article__header {
  font-size: 1rem;
  color: var(--Dark-Grayish);
  margin-bottom: 1.5rem;
  line-height: 1.5;
}
.article__text {
  font-size: 0.75rem;
  line-height: 1.5;
  color: var(--Desaturated-Blue);
}
.article__footer {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  margin-top: 2.125rem;
}
.article__avatar {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
}
.article__sign {
  align-self: end;
  color: #000;
}
.article__sign h2 {
  font-size: 0.75rem;
  margin: 0;
}
.article__sign span {
  font-size: 0.6875rem;
  color: var(--Desaturated-Blue);
}
.article__icon {
  display: grid;
  width: 2rem;
  height: 2rem;
  background: var(--Blue);
  border-radius: 50%;
  align-self: center;
  justify-self: center;
}
.article__icon img {
  display: block;
  align-self: center;
  justify-self: center;
  width: 1.0625rem;
  height: 0.9375rem;
}

.article__icon:hover::after {
  content: "";
  cursor: pointer;
  width: 100px;
  height: 100px;
  top: 0;
  background-color: var(--Dark-Grayish);
}